import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import VueAnalytics from 'vue-analytics' // Google analytics

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
})

Vue.use(VueAnalytics, {
  id: 'UA-79746407-1',
  checkDuplicatedScript: true
})



